import React from "react";
import "./posts.css";
import Post from "../Post/post";

const Posts = (props) => {


    let postElements = props.postsData.map(message =>  <Post message={message.message} likes={message.likes} />);

    return (
        <div className="post">
            <h3>my post</h3>
            <div>
                <div>
                    <textarea/>
                </div>
                <div>
                    <button>Add post</button>
                </div>
            </div>
            <div className="posts">
                {postElements}
            </div>
        </div>

    )
}

export default Posts;